.cg-busy{
  position:inherit !important;
  top:0px;
  left:0px;
  right:0px;
  bottom:0px;
  z-index:1001;
}

.cg-busy-animation.ng-hide-add,
.cg-busy-animation.ng-hide-remove {
  -webkit-transition:all .3s ease;
  -moz-transition:all .3s ease;
  -o-transition:all .3s ease;
  transition:all .3s ease;
  display:block !important;
}
.cg-busy-animation.ng-hide-remove {
  opacity:0;
  -webkit-transform:translate(0px,-40px);
  -moz-transform:translate(0px,-40px);
  -ms-transform:translate(0px,-40px);
  -o-transform:translate(0px,-40px);
  transform:translate(0px,-40px);
}
.cg-busy-animation.ng-hide-remove.ng-hide-remove-active {
  opacity:1;
  -webkit-transform:translate(0px,0px);
  -moz-transform:translate(0px,0px);
  -ms-transform:translate(0px,0px);
  -o-transform:translate(0px,0px);
  transform:translate(0px,0px);
}
.cg-busy-animation.ng-hide-add {
  opacity:1;
  -webkit-transform:translate(0px,0px);
  -moz-transform:translate(0px,0px);
  -ms-transform:translate(0px,0px);
  -o-transform:translate(0px,0px);
  transform:translate(0px,0px);
}
.cg-busy-animation.ng-hide-add.ng-hide-add-active {
  opacity:0;
  -webkit-transform:translate(0px,-40px);
  -moz-transform:translate(0px,-40px);
  -ms-transform:translate(0px,-40px);
  -o-transform:translate(0px,-40px);
  transform:translate(0px,-40px);
}

.cg-busy-backdrop {
  background-color:white;
  opacity:.7;
}

.cg-busy-backdrop-animation.ng-hide-add,
.cg-busy-backdrop-animation.ng-hide-remove {
  -webkit-transition:opacity .3s ease;
  -moz-transition:opacity .3s ease;
  -o-transition:opacity .3s ease;
  transition:opacity .3s ease;
  display:block !important;
}

.cg-busy-backdrop-animation.ng-hide {
  opacity:0;
}

/* All styles below are for the default template. */

.cg-busy-default-wrapper {
  text-align:center;
}

.cg-busy-default-sign{
  display: inline-block;
  position:relative;
  z-index:1002;
  padding-bottom: 6px;
  color:#333333;
  text-shadow:0 1px 1px rgba(255, 255, 255, 0.75);
  background-color:#e9eeee;
  border:1px solid #dddddd;
  border-top-width:0;
  -webkit-border-radius:7px;
  -moz-border-radius:7px;
  border-radius:7px;
  border-top-left-radius:0;
  border-top-right-radius:0;
  -webkit-box-shadow:inset 0 1px 0 rgba(255,255,255,.2), 0 1px 2px rgba(0,0,0,.05);
  -moz-box-shadow:inset 0 1px 0 rgba(255,255,255,.2), 0 1px 2px rgba(0,0,0,.05);
  box-shadow:inset 0 1px 0 rgba(255,255,255,.2), 0 1px 2px rgba(0,0,0,.05);
}
